    function sector() {
        const slideCounter = document.querySelectorAll('.accordion2 .accordion__item');
        ScrollTrigger.create({
            trigger: ".sectors2",
            // markers: true,
            start: "top top",
            // end: "bottom bottom",
            // end: "+=1500px",
            end: () => `+=${(slideCounter[0].offsetHeight * .35) * slideCounter.length}px`,
            // end: () => "+=" + (slideCounter.length * 100) + "%",
            pin: true,
            onUpdate: (indicator) => stepSlide(indicator.progress),
        });

        function stepSlide(progress) {
            let stepProgress = progress * 100;
            let percentageStep = 90 / (slideCounter.length - 1);
            let currentSlide = Math.floor(stepProgress / percentageStep);

            if(percentageStep) {
                slideHide(currentSlide)
                slideShow(currentSlide)
            }
        }

        slideShow(0)
        function slideShow(i) {
            slideCounter[i].classList.add('accordion__item--active')
            if(i >= 1) {
                slideCounter[i - 1].classList.add('accordion__item--effect')
                slideCounter[i - 1].classList.remove('accordion__item--active')
            }
        }
        function slideHide(i) {
            if(i + 1 >= slideCounter.length) {

            } else {
                // slideCounter[i + 1].classList.remove('accordion__item--active')
                slideCounter.forEach(item => {
                    item.classList.remove('accordion__item--active')
                })
            }
            slideCounter[i].classList.remove('accordion__item--effect')
        }
    }







