function filterMobile() {

    let filterHead = document.querySelectorAll('.filter__menu-head');

    filterHead.forEach(item => {
        item.addEventListener('click', () => {
            let content = $(item).next();
            content.slideToggle()
        })
    })

    window.addEventListener('resize', function(event) {
        widthChecked();
    }, true);
}
filterMobile();
